body {
  margin: 0;
  font-family: 'Nunito', -apple-system, BlinkMacSystemFont, 
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  color: #4b4b4b;
}

a {
  text-decoration: none;
  color: #4b4b4b;
}
a:hover {
  color:black;
}

strong {
  color: black;
  font-weight: 800;
}

h1 {
  font-size: 40px;
}

h2 {
  font-weight: 800;
}

ul {
  padding: 0;
  margin: 2px;
}

code {
  font-family:  source-code-pro, Menlo, Monaco, Consolas, 'Nunito',
    monospace;
}

